<script>
import xtermTheme from "xterm-theme";
const themes = Object.keys(xtermTheme);

/*
{
                baudRate: 115200,
                dataBits:8,//7 or 8
                stopBits:1,//1 or 2
                parity:'none',// even or odd
                bufferSize:1024,//must less than 16M
                flowControl:'none',//none||hardware
  }
*/


export default {
  name: "ConfigModal",
  props: {
    visible: Boolean,
    config_type:{type:String,default:"serial"},
  },
  data() {
    return {
        activeName: "second",
        themes: themes,
        theme: window.localStorage.getItem("themeName") || null,
        bgimg: window.localStorage.getItem("bgimg") || null,

        baudRate:[
            {value:9600,label:"9600"},
            {value:14400,label:"14400"},
            {value:19220,label:"19220"},
            {value:28800,label:"28800"},
            {value:38400,label:"38400"},
            {value:57600,label:"57600"},
            {value:115200,label:"115200"},
            {value:230400,label:"230400"},
            {value:460800,label:"460800"},

        ],
        serialConfig:{
            baudRate: 115200,
            dataBits:8,//7 or 8
            stopBits:1,//1 or 2
            parity:'none',// even or odd
            bufferSize:1024,//must less than 16M
            flowControl:'none',//none||hardware
        }

    };
  },
  computed: {
    colors() {
      if (this.theme) {
        return xtermTheme[this.theme];
      } else {
        return null;
      }
    }
  },
  watch: {
    theme(val) {
      window.localStorage.setItem("themeName", val);
      this.$emit("setTheme", xtermTheme[val]);
    }
  },
  methods: {
    handleUpload(event) {
      let file = event.target.files[0];
      var reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = e => {
        this.bgimg = e.target.result;
        window.localStorage.setItem("bgimg", e.target.result);
      };
    },
    handleClick(){

    },
    cancleDialog(){
        this.$emit('update:visible', false)
    },
    Btn_Click(type){
       if(type=='OK'){
          this.$emit('update:visible', false);
          this.$emit("serialConfig", this.serialConfig);
       }
    }

  }
};
</script>

