function writeSerial(term,data){
// eslint-disable-next-line
// debugger
    let self = this;
    let {pane} = term;
    // let tab  = this.tabs[this.currentTab];
    if(!pane.serial){
        return list_serial();
    } //return Promise.reject('!serial')
    if(pane.online == false) {
        pane.serial.close();
        pane.serial = null;
        return list_serial()
    }

    return pane.serial.write(data).then(()=>{
        console.log("writeSerial OK")
    }).catch(err=>{
        console.log("writeSerial",err.message)
        // this.MyLog("writeSerial",err)
    })
}

