<template>
  <div id="app">
    <!-- <TestSerival msg="Welcome to Your Vue.js App"/> -->
    <Xterm msg="Welcome to Your Vue.js App" :onData="onTermData" @serialConfig="serialConfig" ref="xterm"/>
  </div>
</template>

<script>
// import TestSerival from './components/TestSerial.vue'
import Xterm from './components/XTerm.vue'
import {writeSerial,listSerial} from "./components/serial.js"
// 
export default {
  name: 'App',
  components: {
    // TestSerival,
    //openSerial,TermLog
    Xterm
  },
   data() {
        return {
            cache:null,
        }
    },
    methods: {
        onTermData(data,term){
            let {serial,online} = term.pane;
            if(!serial || !online) {
                this.$refs.xterm.showConfigDlg(true,'serial');
                // listSerial.call(this,term);
            }else if(online){
                writeSerial.call(this,term,data);
            }
        },
        serialConfig(value){
            let {conf,pane} = value;
            let {term} = pane;
            listSerial.call(this,term,conf);
        }
  }
}
</script>

