<template>
    <div class="terminal" v-contextmenu:contextmenu id="terminal" @click="handleMove" @mousemove="handleMove" :style="{backgroundColor: bgColor, color: fontColor}">
    <div class="header">
        <span>ไธฒๅฃ็ป็ซฏ</span>
        <ul class="menu-list">
            <li class="active" v-if="false">
                <select class="terminal-select" v-model="currentTab" :style="{backgroundColor: bgColor, color: fontColor }">
                    <option :value="index" v-for="(item, index) in tabs" :key="index">{{ '็ป็ซฏ'+index }}</option>
                </select>
            </li>
            <li class="el-icon-plus" @click="handlePlus"></li>
            <li class="el-icon-delete" @click="handleDelete"></li>
            <li class="el-icon-download" @click="downloadTxt"></li>
        </ul>
    </div>
    <div id="xterm-wrapper">
        <div  v-for="(tab, index) in tabs" :key="index" class="xterm-tabs" v-show="index == currentTab">
            <div v-if="tab.children.length >= 4" class="xterm-tab-item">
                <el-row type="flex" class="el-row-4">
                    <el-col :span="12" v-for="(item, k) in tab.children.slice(0,2)" :key="k" class="el-col">
                        <div class="terminal-pane" :id="item.name"> </div>
                        <div class="serial_status"> {{item.online?'Online':'Offline'}}</div>
                    </el-col>
                </el-row>
                <el-row type="flex" class="el-row-4">
                    <el-col :span="12" v-for="(item, k) in tab.children.slice(2)" :key="k" class="el-col">
                        <div class="terminal-pane" :id="item.name"></div>
                        <div class="serial_status"> {{item.online?'Online':'Offline'}}</div>
                    </el-col>
                </el-row>
            </div>
            <div v-else class="xterm-tab-item">
                <el-row type="flex">
                    <el-col :span="(24/tab.children.length)" v-for="(item, k) in tab.children" :key="k" class="el-col ">
                        <div class="terminal-pane" :id="item.name"> </div>
                        <div class="serial_status"> {{item.online?'Online':'Offline'}}</div>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
    <v-contextmenu ref="contextmenu" class="contextmenu">
        <!-- <v-contextmenu-item @click="handleCreateTab">ๆฐๅปบTab</v-contextmenu-item> -->
        <v-contextmenu-item @click="handleSplitPane">ๅๅฑ</v-contextmenu-item>
        <v-contextmenu-item @click="handleDelete" v-if="tabs.length&&tabs[0].children.length>1">ๅณ้ญ</v-contextmenu-item>
        <v-contextmenu-item @click="dialogVisible = true;config_type='color'">่ฎพ็ฝฎ</v-contextmenu-item>
    </v-contextmenu> 
    <config-modal :visible.sync="dialogVisible" :config_type ="config_type" @setTheme="handleChangeTheme" @serialConfig="serialConfig"></config-modal>
    </div>
</template>

<script>

// import { WebLinksAddon } from "xterm-addon-web-links";
// import Terminal from "./MyXterm";
// import {Terminal,fit,search,weblink} from "./MyXterm";
// import ConfigModal from "./components/Config";

import {Terminal } from 'xterm';
import {v4 as uuidv4 } from "uuid";
import {FitAddon } from 'xterm-addon-fit'
import ConfigModal from './ThemeConfig';
import 'xterm/css/xterm.css'


// import {writeSerial,openSerial,listSerial,TermLog} from "./serial.js"


// import {transfer} from "@/Modules/ymodem";
// import {Serial} from "@/Modules/web_serial";
// import {delay_ms,curr_tm_str} from "@/utils/index";
// import {serial as usb_serial} from "@/Modules/google_serial";


function isInRect(rect, event) {
    if (
        event.clientY >= rect.top &&
        event.clientY <= rect.top + rect.height &&
        event.clientX >= rect.left &&
        event.clientX <= rect.left + rect.width
    ) {
        return true;
    } else {
        return false;
    }
}

export default {
    name: "SerialTerminal",
    props: {
        caption:{type:String,default:""},
        prompt:{type:String,default:"$"},
        onData:{type:Function,default:null},
        onKey:{type:Function,default:null},
    },
    data() {
        return {
            term: null,
            tabs: [],
            config_type:"",
            socket: null,
            currentTab: 0,
            dialogVisible: false,
            theme: window.localStorage.getItem("theme") ?
                JSON.parse(window.localStorage.getItem("theme")) :
                {
                  foreground: "#ECECEC", //ๅญไฝ
                  background: "#000000", //่ๆฏ่ฒ
                  cursor: "help", //่ฎพ็ฝฎๅๆ
                  lineHeight: 20
                },
        };
    },
    computed: {
        bgColor() {
            if (this.theme) {
                return this.theme.background;
            } else {
                return "#000";
            }
        },
        fontColor() {
            if (this.theme) {
                return this.theme.foreground;
            } else {
                return "#fff";
            }
        }
    },
    components: {
        ConfigModal
    },
    methods: {
        createTerminal(container, callback) {
            let self = this;
            let terminalname = "terminal" + uuidv4();

            let term = new Terminal({
                theme: this.theme,
                disableStdin: false, //ๆฏๅฆๅบ็ฆ็จ่พๅฅ
            });
            // term.loadAddon(new weblink());
            term.fitAddon = new FitAddon();
            term.loadAddon(term.fitAddon);
            // term.loadAddon(new search());
            term.prompt = () => {  
                term.write("\x1b[33m$\x1b[0m>")
            }

            term.onKey(e=> {
                let ev = e.domEvent;
                if(ev.key == 'k' && (ev.ctrlKey||ev.metaKey)){
                    term.clear();
                    return true;
                }
                // eslint-disable-next-line
                // debugger
                if(self.onKey) {
                    if(self.onKey(e,term)) return true; 
                }
                // term.prompt();
            })

            let pane = {
                term: term,
                name: terminalname,
                serial:null,
                online:false,
            };

            term.pane = pane;

            term.onData(data => {
                console.log(`onData:[${data.toString()}]`,typeof(data));
                if(self.onData)  self.onData(data,term);
                else{
                    term.write(data)
                } 
            });
            container.children.push(pane);
            container.currentPane = container.children.length - 1;

            callback && callback();

            window.addEventListener("resize", () => {
                term.fitAddon.fit();
            });

            this.$nextTick(() => {
                //term.open(document.getElementById(terminalname));
                let element = document.getElementById(terminalname);
                term.open(element);

                container.children.forEach(item => {
                    let termEle = document.getElementById(item.name);
                    if (item.term.element != termEle.children[0]) {
                        termEle.innerHTML = "";
                        termEle.append(item.term.element);
                    }
                    item.term.fitAddon.fit();
                    item.rect = item.term.element.getBoundingClientRect();
                });
            });
        },
        handlePlus() {
            this.handleSplitPane()
        },
        downloadTxt(){
            let tab = this.tabs[this.currentTab];
            let {term} = tab.children[tab.currentPane];
            if (term.rows === 0) {
                console.log('No output yet');
                return;
            }
            term.selectAll();
            const contents = term.getSelection();
            term.clearSelection();
            const linkContent = URL.createObjectURL(
              new Blob([new TextEncoder().encode(contents).buffer],
                  {type: 'text/plain'}));
            const fauxLink = document.createElement('a');
            fauxLink.download = `terminal_content_${new Date().getTime()}.txt`;
            fauxLink.href = linkContent;
            fauxLink.click();
        },
        async handleDelete() {
            if (this.tabs.length == 1 && this.tabs[this.currentTab].children.length == 1 ) {
                this.$message({
                    type: "warning",
                    message: "ไปๆไธไธชๅๅฑ็ชๅฃ"
                });
                return false;
            } else {

                let tab = this.tabs[this.currentTab];
                let {term,serial} = tab.children[tab.currentPane];

                
                let cont = true;
                if(serial){
                    cont = await this.$confirm("ไธฒๅฃๅทฒ็ปๆๅผ๏ผๆฏๅฆๅณ้ญ?", 'ๆ็คบ', {
                        confirmButtonText: 'ๅณ้ญ',
                        cancelButtonText: 'ๅๆถ',
                        type: 'warning'
                    }).then(() => {
                        return true;
                    }).catch(() =>{
                        return false;
                    })
                }
                if(!cont) return;
                if(serial ) serial.close();//&&serial.close

                term.destroy && term.destroy();
                if (tab.children.length > 1) {
                    tab.children.splice(tab.currentPane, 1);
                    tab.currentPane = tab.children.length - 1;
                    this.$nextTick(() => {
                        tab.children.forEach(item => {
                            let termEle = document.getElementById(item.name);
                            if (item.term.element != termEle.children[0]) {
                                termEle.innerHTML = "";
                                termEle.append(item.term.element);
                            }
                            item.term.fitAddon.fit();
                            item.rect = item.term.element.getBoundingClientRect();
                        });
                        tab.children[tab.currentPane].term.focus();
                    });
                } else {
                    this.tabs.splice(this.currentTab, 1);
                    this.currentTab = this.tabs.length - 1;
                    let tab = this.tabs[this.currentTab];
                    tab.currentPane = tab.children.length - 1;
                    tab.children[tab.currentPane].term.focus();

                    this.$nextTick(() => {
                        this.tabs.forEach(tab => {
                            tab.children.forEach(item => {
                                let termEle = document.getElementById(item.name);
                                if (item.term.element != termEle.children[0]) {
                                    termEle.innerHTML = '';
                                    termEle.append(item.term.element);
                                }
                                item.term.fitAddon.fit();
                                item.rect = item.term.element.getBoundingClientRect();
                            });
                            tab.children[tab.currentPane].term.focus();
                        });
                    });
                }
            }
        },
        handleSplitPane() {
            // ๅๅฑ
            let tab = this.tabs[this.currentTab];
            if (tab.children.length >= 4) {
                this.$message({
                    type: "warning",
                    message: "ๅๅฑๅทฒ็ป่พพๅฐๆๅคงๆฐ้"
                });
                return false;
            }
            this.createTerminal(tab, null);
        },

        handleCreateTab() {
            // ๆฐๅปบTab
            let tab = {
                name: "tab0",
                children: []
            };
            this.createTerminal(tab, () => {
                this.tabs.push(tab);
                this.currentTab = this.tabs.length - 1;
            });
        },

        handleMove(event) {
            let tab = this.tabs[this.currentTab];
            if(!tab) return;
            let pane = tab.children[tab.currentPane];
            if (isInRect(pane.rect, event)) {
                return false;
            }
            if(this.dialogVisible){
                return false;
            }
            tab.children.forEach(({term, rect }, i) => {
                if (isInRect(rect, event)) {
                    term.focus();
                    tab.currentPane = i;
                } else {
                    term.blur();
                }
            });
        },

        handleChangeTheme(val) {
            this.theme = val;
            let tab  = this.tabs[this.currentTab];
            let pane = tab.children[tab.currentPane];
            if(pane) pane.term._core.options.theme = val;
        
            window.localStorage.setItem("theme", JSON.stringify(val));
        },
        showConfigDlg(show,type=""){
            this.config_type = type;
            this.dialogVisible = show;
        },
        serialConfig(conf){
            if(this.config_type == 'serial'){
                let tab  = this.tabs[this.currentTab];
                let pane = tab.children[tab.currentPane]; 
                this.$emit('serialConfig',{conf,pane});
            }

        },
        close() {
            if (this.tabs.length > 0) {
                this.tabs.forEach(tab => {
                    if (tab.children) {
                        tab.children.forEach(({term}) => {
                            term.destroy && term.destroy();
                        });
                    }
                });
            }
        },
    },
    created() {
        window.onkeydown = window.onkeyup = window.onkeypress = () => {
            let _key = window.event.keyCode;
            if (_key === 70 && window.event.ctrlKey) {
                window.event.returnValue = false;
                let tab = this.tabs[this.currentTab];
                tab.children[tab.currentPane].term.findNext("go", {
                    incremental: true
                });
                return false;
            }
        };
        window.onunload = () => {
            this.close();
        };
    },
    mounted() {
        if (this.tabs.length == 0) {
            let tab = {
                name: "tab0",
                children: []
            };
            this.createTerminal(tab, () => {
                this.tabs.push(tab);
                this.currentTab = this.tabs.length - 1;
            });
        }
    },
    beforeDestroy() {
        this.close();
    }
};
</script>

